.searchbar{
    display: flex;
    align-items: center;
    height: 76px;
}

.searchBox{
    width: 80%;
    background-color: #F5F5F5;
    border-radius: 15px;
}

h3{
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}

img{
    width:4rem;
    border-radius:50%; 
}

p{  
    overflow: hidden;
    cursor: pointer;
}

.content{
    text-wrap: nowrap;
    width: 300px !important;
    overflow: hidden !important;
    color: rgb(178, 172, 172);
}
