.App {
  background-color: lightgray;
  height: 100vh;
}

.searchbar {
  background-color: white;
}

.container {
  background-color: #d3d3d3;
  overflow: hidden;
  box-shadow: 0px 0px 1.7px rgba(0, 0, 0, 0.07),
    0px 0px 4.3px rgba(0, 0, 0, 0.048), 0px 0px 8.9px rgba(0, 0, 0, 0.039),
    0px 0px 18.3px rgba(0, 0, 0, 0.031), 0px 0px 50px rgba(0, 0, 0, 0.022);
}

.contact {
  border-right: 1px solid rgb(210, 206, 206);
}

.contact-box {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 35rem;
  scrollbar-width: thin;
}

.options {
  justify-content: end;
  padding-right: 20px;
}
.options:only-child {
  cursor: pointer;
}

span {
  padding-right: 10px;
}

.secdhalf {
  background-image: url("./assets/MessageBg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.ChatBox {
  height: 32rem;
  overflow-y: scroll;
  overflow-x: hidden;
  scrollbar-width: thin;
}

button {
  background-color: white;
  border: 0;
}

.material-symbols-outlined:hover {
  cursor: pointer;
  border-radius: none;
}

.msgCont {
  text-align: end;
  text-wrap: wrap;
  background-color: rgb(120, 227, 120);
  border-radius: 10px;
  margin-left: 20px;
}

.msgBar {
  text-wrap: wrap;
  max-width: 400px;
}

.deltbtn {
  font-size: 22px;
}

.senderCont {
  text-wrap: wrap;
  background-color: #fff;
  border-radius: 10px;
  margin-left: 20px;
}

.deltbtn:hover {
  text-shadow: rgba(255, 0, 0, 1) 0px 0px 2px;
}
.card-title:hover {
  cursor: pointer;
}


